.main {
  height: 880px;
  background-color: #041827;
  background: url("../img/main-background.jpg") no-repeat center/cover;

  @media (max-width: 1030px) {
    background: url("../img/main-background-tablet.jpg") no-repeat center/cover;


  @media (min-resolution: 2dppx) {
    background: url("../img/main-background-tablet-2x.jpg") no-repeat center/cover;
  }
}

  @media (max-width: 552px) {
    background: url("../img/main-background-mobile.jpg") no-repeat center/cover;

  @media (min-resolution: 2dppx) {
    background: url("../img/main-background-mobile-2x.jpg") no-repeat center/cover;
  }
}
}


.main__container {
  height: 880px;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
  row-gap: 40px;
}

.main__title {
  margin: 0;
  margin-left: 150px;
  color: #f2f5f6;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
}

.main__title + span {
  margin: 0;
  margin-left: 150px;
  color: #f2f5f7;
  font-family: 'PT Mono', "Arial", sans-serif;
  font-weight: 400;
  font-size: 22px;
  line-height: 155%;
  max-width: 685px;
  display: flex;
  margin-top: 40px;
}

.main__button,
.main__button--mobile {
  background-color: #00B2FF;
  border: none;
  padding: 15px 25px;
  font-family: 'Roboto Condensed', "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.05em;
  margin-left: 150px;
  margin-top: 55px;
}

.button:hover {
  background-color: #041427;
}

.main__button--mobile {
  display: none;
}

@media (max-width: 1030px) {
  .main__title {
    font-size: 64px;
    line-height: 75px;
    margin-left: 100px;
    letter-spacing: 0.07em;
  }

  .main__title + span {
    font-size: 22px;
    margin-left: 100px;
  }

  .main__button {
    margin-left: 100px;
  }
}

@media (max-width: 552px) {

  .main {
    height: 560px;
  }

  .main__container {
    height: 474px;
    padding-left: 23px;
    row-gap: 0;
  }

  .main__wrapper {
    display: flex;
    flex-direction: column;
    max-height: 178px;
    height: 100%;
  }

  .main__title {
    font-size: 36px;
    line-height: 42px;
    margin-left: 0;
  }

  .main__title + span {
    font-size: 14px;
    margin-left: 0;
    margin-top: 9px;
  }

  .main__button {
    display: none;
  }

  .main__button--mobile {
    display: flex;
    margin-left: 0;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    justify-content: center;
    margin-top: 47px;
    padding: 11px 25px;
  }
}
